<template>
  <div class="home">

    <div class="title">
      {{ '书屋数据' }}
    </div>
    <a-row class="info" type="flex" :gutter="[24, 24]">
      <template v-for="(item, index) in bookInfo">
        <a-col :xs="20" :md="12" :lg="6" :key="index" style="margin: 0 auto;">
          <div class="class_style" :style="{background: item.bgColor}">
            <div class="card_title">{{item.title}}</div>
            <div class="card_info">{{item.count}}{{item.classifier}}</div>
          </div>
        </a-col>
      </template>
    </a-row>
    <div class="title">
      {{ '书籍占比' }}
    </div>
    <div class="echarts">
      <a-row :gutter="[16, 16]">
        <a-col :xl="10" :lg="24">
          <div id="book_proportion"></div>
        </a-col>
        <a-col :xl="10" :lg="24">
          <div id="book_count"></div>
        </a-col>
      </a-row>
    </div>
  </div>
</template>
<script>
import { Row, Col } from 'ant-design-vue'
import { mapActions, mapState } from 'vuex'
export default {
  name: 'Home',
  components: { ARow: Row, ACol: Col },
  data() {
    return {
    }
  },
  computed: {
    ...mapState('home', ['accountData']),
    bookInfo () {
      return [
        { title: '图书分类', bgColor: '#2CA9B3', count: this.accountData && this.accountData.cate_count, classifier: '类'},
        { title: '书籍数量', bgColor: '#EAAB78', count:  this.accountData && this.accountData.book_count, classifier: '本'},
        { title: '读者数', bgColor: '#6CAFEA', count:  this.accountData && this.accountData.user_count, classifier: '人'},
        { title: '管理员人数', bgColor: '#96ADF7', count:  this.accountData && this.accountData.manager_count, classifier: '人'},
      ]
    },
    pipeData() {
      return this.accountData && this.accountData.book_category && this.accountData.book_category.map(item => {
        return {
          name: item.category_name,
          value: item.book
        }
      })
    },
    histogramData() {
      return style => {
        return this.accountData && this.accountData.book_category && this.accountData.book_category.map(item => {
          return { name: item.category_name, type: 'bar', data: [item.book], itemStyle: style }
        })
      } 
    }
  },
  watch: {
    accountData() {
      this.$nextTick(() => {
        this.getPie()
      })
    }
  },
  methods: {
    ...mapActions('home', {
      getAccountData: 'getAccountData'
    }),
    getPie() {
      // 绘制图表
      const book_proportion = this.$echarts.init(document.getElementById('book_proportion'))
      const book_count = this.$echarts.init(document.getElementById('book_count'))

      const barItemStyle = {
        normal: {
          label: {
            formatter: '{a}: {c}'+'本',
            show: true,
            position: "right",
            textStyle: {
              fontWeight: "bolder",
              fontSize: "12",
              color: "#000"
            }
          }
        }
      } 

      // 指定图表的配置项和数据
      let book_proportion_option = {
        //鼠标划过时饼状图上显示的数据
        tooltip: {
          trigger: 'item',
          formatter: '{b}: {c} ({d}%)'
        },
        //图例
        legend: {
          // orient: 'vertical',//图例的显示方式  默认横向显示
          bottom: 10,//控制图例出现的距离  默认左上角
          left: 'center',//控制图例的位置
          // itemWidth: 16,//图例颜色块的宽度和高度
          // itemHeight: 12,
          textStyle: {
            color: '#000',
            fontSize: 16
          },
          data: this.accountData && this.accountData.book_category && this.accountData.book_category.map(item => {
            return item.category_name
          })//图例上显示的饼图各模块上的名字
        },
        //饼图中各模块的颜色
        color: ['#32dadd', '#b6a2de', '#5ab1ef', '#abcdef', '#aaa'],
        // 饼图数据
        series: {
          type: 'pie',             //echarts图的类型   pie代表饼图
          radius: '70%',           //饼图中饼状部分的大小所占整个父元素的百分比
          center: ['50%', '50%'],  //整个饼图在整个父元素中的位置
          data: this.pipeData,
        }

      }
      let book_count_option = {
        //鼠标划过时饼状图上显示的数据
        tooltip: {
          trigger: 'item',
          formatter: '{a}: {c}'
        },
        //图例
        legend: {
          // orient: 'vertical',//图例的显示方式  默认横向显示
          bottom: 10,//控制图例出现的距离  默认左上角
          left: 'center',//控制图例的位置
          // itemWidth: 16,//图例颜色块的宽度和高度
          // itemHeight: 12,
          textStyle: {
            color: '#000',
            fontSize: 14
          },
        },
        //饼图中各模块的颜色
        color: ['#32dadd', '#b6a2de', '#5ab1ef', '#abcdef', '#aaa'],
        yAxis: {
          type: 'category',
        },
        xAxis: {
          show: false,
          boundaryGap: ['5%', '5%']
        },
        series: this.histogramData(barItemStyle),
      }
      // 使用刚指定的配置项和数据显示图表。
      book_proportion.setOption(book_proportion_option)
      book_count.setOption(book_count_option)
    }
  },
  mounted() {
    this.getAccountData()
    this.$nextTick(() => {
      this.getPie()
    })
  }
}
</script>
<style lang="scss" scoped>
.home {
  .title {
    font-size: 1.5em;
    font-weight: 600;
    margin-bottom: 15px;
  }
  .info {
    margin-bottom: 25px !important;
    .class_style {
      text-align: center;
      padding: 20px 0;
      color: #fff;
      .card_title {
        font-size: 1.8em;
        margin-bottom: 5px;
        font-weight: 600;
      }
      .card_info {
        font-size: 1.4em;
      }
    }
  }
  .ant-row {
    width: 100%;
  }
  .echarts {
    width: 100%;
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    #book_proportion {
      // width: 500px;
      // width: 100%;
      min-height: 500px;
    }
    #book_count {
      // width: 500px;
      // width: 100%;
      min-height: 500px;
    }
  }
}
</style>